.card{
    font-family: 'Raleway', sans-serif;
    width: 350px;
    height: 400px;
    background: linear-gradient(106.5deg, rgba(30, 30, 30, 0.91) 23%, rgba(0, 0, 0, 0.8) 93%);;
    
    margin-top: 70px;
    margin-bottom: 70px;
    font-size: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
}

.cardsdiv{
    display: flex;
    justify-content: space-evenly;
    
}
.text p{
    padding: 20px;
    text-align: center;
    margin: auto;
    margin-top: 20px;
    width: 300px;
    font-size: 18px;
    color: white;
    
}
.text h1{
    text-align: center;
    margin-bottom: -25px;
    color: white;
}
#react, #javascript, #python{
    width: 70px;
}
.image{
    text-align: center;
    margin-top: 5px;
}
.projects{
    font-family: 'Raleway', sans-serif;
    text-align: center;
    margin-bottom: 40px;
}
#showmeBtn{
    padding: 10px;
    
    background-color: palevioletred;
    border-style: none;
    outline-style: none;
    background-size: 140px;
    height: 60px;
    border-radius: 30px;
    color: white;
    font-size: 18px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: padding-left 0.3s, padding-right 0.3s;
}
#showmeBtn:hover{
    padding-left: 20px;
    padding-right: 20px;
}
.projects h1{
    margin-bottom: 30px;
}
@media only screen and (max-width: 800px){
    .cardsdiv{
        display: block;
        
    }
    .card{
        margin: auto;
        margin-bottom: 20px;
    }
    .projects h1{
        margin-top: 60px;
    }
}
